.user-list {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    padding: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: baseline;
    flex-direction: column;
    align-content: center;
    padding-top: 70px;
    text-align: center;

    .user {
        cursor: pointer;
        width: 100px;  // Default to full width
        overflow: hidden; // Or flex might break
        list-style: none;
        flex-basis: 100%;
        margin-bottom: 20px;

        p {
            color: #121212;
        }

        .text {
            width: 100%;

            p {
                text-transform: capitalize;
                font-size: 18px;
                font-weight: bold;

                &.name {
                    font-size: 20px;
                    margin: 8px 0 5px;
                }

                &.eng {
                    font-size: 16px;
                    margin: 0;
                }
            }
        }

        .image {
            width: 100%;
            border-radius: 50%;
        }
    }
}

.modal-content {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;

    input {
        height: 48px;
        line-height: 48px;
        border: 1px solid black;
        width: 90%;
        padding: 0 12px;
        font-size: 24px;
    }

    span {
        display: flex;
        line-height: 48px;
        font-size: 28px;
        color: white;
        background: #3880ff;
        border-radius: 10px;
        width: 90%;
        margin-bottom: 15px;
        text-align: center;
        justify-content: center;
    }
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-bottom: 8px;

    .loginButton {
        line-height: 62px;
        background: #3880ff;
        color: white;
        text-align: center;
        font-size: 26px;
        width: 48%;
        float: left;
    }

    .back {
        background: #f1f1f1;
        color: #121212;
        margin-right: 0;
    }
}

form {
    width: 100%;
    text-align: center;
}

.error {
    color: rgb(244, 83, 83);
    font-size: 20px;
    font-weight: bold;
    line-height: 24px;
    display: none;

    &.show {
        display: block;
    }
}