.bottomBar {
    display: flex;
    flex-direction: row;
    position: fixed;
    width: 100%;
    bottom: 0;
    background: white;
    border-top: 1px solid #080f1b;
    box-shadow: 0 2px 4px 0 #D1D6E6, 0 12px 32px 0 #000000;
    justify-content: center;

    .tab {
        display: flex;
        align-items: center;
        width: 20%;
        line-height: 48px;
        font-size: 16px;
        text-align: center;
        justify-content: center;
        border-right: 1px solid rgba(0,0,0,0.1);

        &.hidden {
            display: none;
        }

        &:last-child {
            border-right: 0;
        }

        .item {
            text-align: center;
        }
    }
}